import React from 'react';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import Home from './pages/Home';
import About from './pages/About';
import Contacts from './pages/Contacts';
import Journal from './pages/Journal';
import JournalSinglePost from './pages/JournalSinglePost';
import SearchResults from './pages/SearchResults';
import Works from './pages/Works';
import WorksInside1 from './pages/WorksInside1';
import WorksInside2 from './pages/WorksInside2';
import WorksInside3 from './pages/WorksInside3';
import WorksInside4 from './pages/WorksInside4';
import WorksInside5 from './pages/WorksInside5';
import WorksInside6 from './pages/WorksInside6';
import WorksInside7 from './pages/WorksInside7';
import WorksInside8 from './pages/WorksInside8';
import page404 from './pages/404';
import UserInterface from './pages/UserInterface';

function App() {
    return (
        <Router>
            <Switch>
                <Route
                    exact
                    path={ `${ process.env.PUBLIC_URL + "/" }` }
                    component={ Home }
                />
                <Route
                    exact
                    path={ `${ process.env.PUBLIC_URL + "/about" }` }
                    component={ About }
                />
                <Route
                    exact
                    path={ `${ process.env.PUBLIC_URL + "/about" }` }
                    component={ About }
                />
                <Route
                    exact
                    path={ `${ process.env.PUBLIC_URL + "/contacts" }` }
                    component={ Contacts }
                />
                <Route
                    exact
                    path={ `${ process.env.PUBLIC_URL + "/journal" }` }
                    component={ Journal }
                />
                <Route
                    exact
                    path={ `${ process.env.PUBLIC_URL + "/journal-single-post" }` }
                    component={ JournalSinglePost }
                />
                <Route
                    exact
                    path={ `${ process.env.PUBLIC_URL + "/search-results" }` }
                    component={ SearchResults }
                />
                <Route
                    exact
                    path={ `${ process.env.PUBLIC_URL + "/works" }` }
                    component={ Works }
                />
                <Route
                    exact
                    path={ `${ process.env.PUBLIC_URL + "/ui" }` }
                    component={ UserInterface }
                />
                <Route
                    exact
                    path={ `${ process.env.PUBLIC_URL + "/works-inside1" }` }
                    component={ WorksInside1 }
                />
                <Route
                    exact
                    path={ `${ process.env.PUBLIC_URL + "/works-inside2" }` }
                    component={ WorksInside2 }
                />
                <Route
                    exact
                    path={ `${ process.env.PUBLIC_URL + "/works-inside3" }` }
                    component={ WorksInside3 }
                />
                <Route
                    exact
                    path={ `${ process.env.PUBLIC_URL + "/works-inside4" }` }
                    component={ WorksInside4 }
                />
                <Route
                    exact
                    path={ `${ process.env.PUBLIC_URL + "/works-inside5" }` }
                    component={ WorksInside5 }
                />
                <Route
                    exact
                    path={ `${ process.env.PUBLIC_URL + "/works-inside6" }` }
                    component={ WorksInside6 }
                />
                <Route
                    exact
                    path={ `${ process.env.PUBLIC_URL + "/works-inside7" }` }
                    component={ WorksInside7 }
                />
                <Route
                    exact
                    path={ `${ process.env.PUBLIC_URL + "/works-inside8" }` }
                    component={ WorksInside8 }
                />
                <Route exact component={ page404 }/>
            </Switch>
        </Router>
    );
}

export default App;
